ن و برای اخذ وقت سفارت و عملیات انگشت‌گاری و تحویل مدارک شما باید به کشورهای اطراف مراجعه کنید به طور مثال کشور کانادا طبق قوانین سفارتی خودش این وظیفه رو به عهده وک (VAC) گذاشته که مخفف این عبارت هست Visa Application Center و مردم ایران میتونن برای اخذ ویزا به این مراکز در کشورهای همسایه مراجعه کنن. اخذ وقت از این مراکز هم به صورت حضوری و هم به صورت آنلاین امکان‌پذیر هست یعنی متقاضی هم میتونه به صورت مستقیم به این مراکز مراجعه کنه و حضوری تمام مدارکشو ارائه بده و هم میتونه به صورت آنلاین و بدون خروج از کشور درخواست تعیین وقت برای بررسی مدارکش رو ثبت کنه که خب مزایای بیشتری هم نسبت به مراجعه حضوری داره که از این بین میشه به سرعت بیشتر بررسی مدارک و متعاقباً انجام سریعتر امور مرتبط اشاره کرد، تنها عیب این حالت هم این هست که شما باید بتونین هزینه وقت سفارت رو از طریق کارت‌های بین المللی واریز کنین. ")]),_c('h3',{staticClass:"d-inline-block font-small-title-xs mt-2 mt-sm-4 mt-lg-6"},[_vm._v("چگونه وقت سفارت بگیریم:")]),_c('p',{staticClass:"text-justify answerText font-small-xs mt-2 mt-sm-4 mt-lg-6"},[_vm._v(" بسته به اینکه متقاضی یا مسافر میخواد به کجا و به چه حوزه‌ای از کشورها مسافرت کنه این سوال جواب‌های متفاوتی داره اما به هر صورت متخصصان ما در آهوان با پشتوانه سال‌ها تجربه آماده ارائه مشاوره و انتقال تجربیاتشون در این مسیر به مسافران هستن. ")]),_c('h3',{staticClass:"d-inline-block font-small-title-xs mt-2 mt-sm-4 mt-lg-6"},[_vm._v("مراحل اخذ وقت سفارت:")]),_c('p',{staticClass:"text-justify answerText font-small-xs mt-2 mt-sm-4 mt-lg-6"},[_vm._v(" مهمترین و حیاتی‌ترین مرحله، آماده‌سازی مدارک از تهیه تا ترجمشون هست که حتماً باید تحت نظارت متخصصان و به فراخور هدف مدنظر مسافر یا متقاضی از سفر انجام بگیره چه بسا ارائه یک مدرک از نظر متقاضی امتیاز محسوب بشه ولی به عنوان عامل ریجکتی عمل کنه. مرحله بعد تحویل مدارک به سفارت هست که بعد از اون بررسیش بین دو تا چهار هفته زمان میبره که بعد از اون متقاضی میتونه برای طی مراحل بعدی جهت اخذ ویزا گام برداره یا در صورت ریجکتی با تصحیح مدارک و یا رفع ابهامات و ایرادات دوباره اقدام کنه. ")])])])],1)
